<template>
    <div>
        <adt-search-folder
            :searchRouter="{query: {accountName: search.accountName, organizationId: search.organizationId, phoneNumber: search.phoneNumber}}">
            <template slot="always-show">
                 <Form ref="formSearch" :model="search" :rules="ruleSearch" inline :label-width="100" label-position="left">
                    <Row>
                        <Col span="6">
                            <FormItem prop="departurePort" label="启运地">
                                <Input class="search-input" v-model="search.departurePort" clearable></Input>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="destinationPort" label="目的地">
                                <Input class="search-input" v-model="search.destinationPort" clearable></Input>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="cargoType" label="货物类型">
                                <Select class="search-input" v-model="search.cargoType" clearable>
                                    <Option value="beijing">New York</Option>
                                    <Option value="shanghai">London</Option>
                                    <Option value="shenzhen">Sydney</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="trspMode" label="运输方式">
                                <Select class="search-input" v-model="search.trspMode" clearable>
                                    <Option value="beijing">New York</Option>
                                    <Option value="shanghai">London</Option>
                                    <Option value="shenzhen">Sydney</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="trspType" label="运输类型">
                                <Select class="search-input" v-model="search.trspType" clearable>
                                    <Option value="beijing">New York</Option>
                                    <Option value="shanghai">London</Option>
                                    <Option value="shenzhen">Sydney</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="contaDimensionType" label="集装箱尺寸类型">
                                <Select class="search-input" v-model="search.contaDimensionType" clearable>
                                    <Option value="beijing">New York</Option>
                                    <Option value="shanghai">London</Option>
                                    <Option value="shenzhen">Sydney</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="contaSource" label="集装箱来源">
                                <Select class="search-input" v-model="search.contaSource" clearable>
                                    <Option value="beijing">New York</Option>
                                    <Option value="shanghai">London</Option>
                                    <Option value="shenzhen">Sydney</Option>
                                </Select>
                            </FormItem>
                        </Col>
                        <Col span="6">
                            <FormItem prop="temff" label="时效">
                                <Input class="search-input" v-model="search.temff" clearable></Input>
                            </FormItem>
                        </Col>
                    </Row>
                </Form>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <!-- 客户/销售看到的服务方案列表-没有删除 -->
            <AdtButton v-if="distinguishCharacter == 'cstm' || distinguishCharacter == 'mkt'">
                <Button @click="add()">新增货运要求<Icon type="ios-add-circle-outline" /></Button>
            </AdtButton>
            <!-- 调度看到的服务方案列表-有批量删除 -->
            <AdtButton v-if="distinguishCharacter == 'dspt'" :allDelShow="true" :delUrl="delUrl" :selectList="selectList">
                <Button @click="add()">新增货运要求<Icon type="ios-add-circle-outline" /></Button>
            </AdtButton>
            <adt-paginator class="pagination" :currentPage="~~page" :total="~~total" path="/authority-manage/user-manage" />
        </div>
        <div class="public-component-line"></div>
        <div class="public-list">
            <div class="public-component-card" v-for="(logistics, id) in listData" :key="id">
                <AdtListItem :logistics="logistics" :showCheckbox="distinguishCharacter == 'dspt'" @checkData="checkData">
                    <template slot="tab-title">
                        <span >物流服务方案编号:{{ logistics.logisticsSchemeCode }}</span>
                    </template>
                    <template slot="tab-operat">
                        <Poptip placement="right-start" class="card-icon">
                            <span>编辑</span>
                            <Icon type="md-arrow-dropdown" />
                            <div class="card-poptip-font" slot="content">
                                <p @click="edit(logistics)">编辑</p>
                                <p @click="detail(logistics)">详情</p>
                                <p @click="remove(logistics)" class="failure">删除</p>
                            </div>
                        </Poptip>
                    </template>
                    <template slot="tab-content">
                        <div class="card-route">
                            <div class="card-content" v-for="(location, index) in logistics.routeLists" :key="index">
                                <span class="card-outset">{{ location.departurePort }}</span>
                                <template v-if="index === logistics.routeLists.length - 1">
                                    <span class="card-outset">{{ location.destinationPort }}</span>
                                </template>
                                <div class="card-transit">
                                    <div class="card-way">
                                        <svg class="icon" aria-hidden="true">
                                            <use v-if="location.trspMode == '陆运'" fill="#FFBB32" xlink:href="#icon-andaotongcaidanICON_huoche"></use>
                                            <use v-if="location.trspMode == '海运'" fill="#FFBB32" xlink:href="#icon-andaotongcaidanICON_lunchuan"></use>
                                        </svg>
                                    </div>
                                    <div class="card-line">
                                        <svg class="icon" aria-hidden="true">
                                            <use fill="#02ADB5" xlink:href="#icon-andaotongcaidanICON_jiantou3"></use>
                                        </svg>
                                    </div>
                                    <div class="card-genre">{{ location.logisticsType }}{{ location.trspType }}</div>
                                </div>
                                <!-- <template v-if="index === logistics.routeLists.length - 1">
                                    <span style="float: left;">{{ location.destinationPort }}</span>
                                </template> -->
                            </div>
                        </div>
                    </template>
                    <template slot="tab-bottom">
                        <!-- 客户看到的最高时效/价格 -->
                        <div class="card-bottom" v-if="distinguishCharacter == 'cstm'">
                            <span>时效: {{ logistics.hghtTmeff }}{{ logistics.tmeffUnit}}</span>
                            <span></span>
                            <span class="card-right">
                                <span>价格:</span>
                                <span class="card-right-size">{{ logistics.lwstPrice }}</span>
                                <span>/{{logistics.offerCurr}}</span>
                            </span>
                        </div>
                        <!-- 调度/销售看到的时效段/价格区间 -->
                        <div class="card-bottom"  v-if="distinguishCharacter == 'mkt' || distinguishCharacter == 'dspt'">
                            <span>时效: {{ logistics.lwstTmeff }}-{{ logistics.hghtTmeff }}{{ logistics.tmeffUnit}}</span>
                            <span></span>
                            <span class="card-right">
                                <span>价格:</span>
                                <span class="card-right-size">{{ logistics.lwstPrice }}-{{ logistics.hghtPrice }}</span>
                                <span>/{{logistics.offerCurr}}</span>
                            </span>
                        </div>
                    </template>
                </AdtListItem>
            </div>
        </div>
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
import validateRules from '@/libs/validateRules'
import AdtListItem from '@/components/adt-list-item/Index'
import { apiGetSchemeList } from '@/api/logistics'
export default {
    mixins: [paginator],
    components: {
        AdtListItem
    },
    data() {
        return {
            formSearch: {},
            delUrl: apiGetSchemeList,
            selectList: {},
            distinguishCharacter: '',
            ruleSearch: {
                departurePort: [validateRules.required('启运地')[0]],
                destinationPort: [validateRules.required('目的地')[0]],
                cargoType: [validateRules.select('货物类型')[0]]
            },
            search: {
                departurePort: '',
                destinationPort: '',
                trspMode: '',
                cargoType: '',
                temff: '',
                trspType: '',
                contaDimensionType: '',
                contaSource: ''
            },
            searchClick: '',
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ],
            enableStatusList: [
                {code: 'unreviewed', status: '已启用'},
                {code: 'stock', status: '未启用'},
                {code: 'progress', status: '已启用'}
            ],
            listData: [
                {
                    statusCode: 'disagree',
                    status: '需求未同意',
                    logisticsPlanNumber: '物流方案编号1',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            city: '重庆1',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    statusCode: 'agree',
                    status: '方案已同意',
                    logisticsPlanNumber: '物流方案编号2',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            city: '重庆2',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    statusCode: 'refuse',
                    status: '已拒绝',
                    logisticsPlanNumber: '物流方案编号3',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            city: '重庆2',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            city: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                }
            ]
        }
    },
    mounted() {
        // 取本地的账号角色信息
        // this.JSON.parse(window.sessionStorage.getItem('goodsData'))
        this.distinguishCharacter = 'dspt'
    },
    methods: {
        dataInit (val) {
            this.getData(val)
        },
        getData (val) {
            const data = {
                size: 6,
                page: this.page,
                // departurePort: this.search.departurePort,
                // destinationPort: this.search.destinationPort,
                // cargoType: this.search.cargoType,
                trspMode: this.search.trspMode,
                departurePort: '重庆',
                destinationPort: '北京',
                cargoType: '普货',
                temff: this.search.temff,
                trspType: this.search.trspType,
                contaDimensionType: this.search.contaDimensionType,
                contaSource: this.search.contaSource,
                flag: 'true'
            }
            apiGetSchemeList({ data: data }).then( res => {
                console.log(res)
                if (res.code == 200) {
                    this.listData = res.data.content || []
                }
            })
            // if (val === 'judgment') {
            //     data.flag = 'false'
            //     this.getSchemeListData(data)
            // } else {
            //     data.flag = 'true'
            //     this.$refs["formSearch"].validate( (valid) => {
            //         if (valid) {
            //             this.getSchemeListData(data)
            //         }
            //     })
            // }
        },
        add () {
            if (this.distinguishCharacter == 'cstm' ) {
                // 如果是客户，跳转至简易货运要求页面
                this.$router.push({ path:'/freight-control/freight-request/list/1' })
            }
            if (this.distinguishCharacter == 'mkt' || this.distinguishCharacter == 'dspt') {
                // 如果是销售/调度，跳转至复杂货运要求页面
                this.$router.push({ path:'/freight-control/freight-request/list/1' })
            }
        },
        checkData (data, status) {
            console.log(data, status)
        },
        edit (val) {
            const data = {
                logisticsSchemeCode: val.logisticsSchemeCode,
                roleType: 'roleCstm'
            }
            this.$router.push({ path: '/logistics-solutions/scheme-details', query:{logisticsData: data}})
        },
        detail (val) {
            const data = val
            window.sessionStorage.setItem('logisticsData', JSON.stringify(data))
            this.$router.push({ path: '/logistics-solutions/scheme-details'})
        },
        remove (val) {}
    },
}
</script>

<style lang="scss" scoped>
.search {
    margin-left: 30px;
}
.search-input {
    width: 160px;
}
.line-margin {
    margin-left: 30px;
}
.icon-action{
    margin: 0 10px;
}
.btn-pagina{
    display: flex;
    padding: 20px 0;
    .pagination{
        width: 50%;
        text-align: right;
        margin-right: 2%;
    }
}
</style>
